          <h3>「第五人格」联动「伊藤润二惊选集」，限定皮肤上线</h3>
          <p class="meta">豌豆荚新游 · 12 月 19 日 18:01:12</p>
          <p class="desc">伊藤润二老师笔下的诅咒之子双一、黑白美少年，将会以限定皮肤的形式，将出现在欧利蒂斯庄园里。</p>
