      <div style="height:40px;"></div>
      <div class="content">
        <p>「第五人格」和著名恐怖动画「伊藤润二惊选集」达成合作，12月20日，伊藤润二老师笔下的诅咒之子双一、黑白美少年，将会以限定皮肤的形式，将出现在欧利蒂斯庄园里。</p>
        <p>
