        >
        </video-player>
        </p>
        <p><strong>双一：口含铁钉 诅咒之子</strong></p>
        <p><strong><img alt="" src="https://android-imgs.25pp.com/fs08/2018/12/19/6/686756e295b0ec666cf8425bf4fe74b1.png" class="content-single-image"></strong></p>
        <p>作为伊藤润二老师笔下的代表性角色，双一在平日生活中，是不受待见的阴郁少年。学校里，几乎没有朋友，经常孤零零一个人坐在位子上自言自语，回到家里后，则成为了哥哥姐姐们嫌弃的对象。或许就是在这样一种环境下，日渐造成了双一近乎于扭曲的性格，以及强烈的报复心理。</p>
        <p>和其他少年不同，双一似乎与身俱来就拥有强大的预言和诅咒的能力，对于神秘事物，如妖魔鬼怪等十分感兴趣。他的标志性动作，就是满嘴含着铁钉，似乎享受那种咀嚼的快感。而在日常生活中，双一往往会通过诅咒人偶娃娃，甚至对它钉钉子的方式，对身边的同学进行恶毒的诅咒，造成活埋等令人发指的行为。</p>
        <p>看似不受重视、默默无闻，实则暗藏玄机，双一的人设很容易让人想到「第五人格」求生者幸运儿。所以在本次联动内容中，双一也会以幸运儿限定皮肤的形式出现，让玩家用他出阵演绎。</p>
        <p><img alt="" src="https://android-imgs.25pp.com/fs08/2018/12/19/6/1240f45bc09892f2584123ea662d85a9.png" class="content-single-image"></p>
        <p><strong>十字路口的美少年：黑白之间 占卜谜局</strong></p>
        <p><strong><img alt="" src="https://android-imgs.25pp.com/fs08/2018/12/19/10/99383641b65d2266aa49fac50370b695.png" class="content-single-image"></strong></p>
        <p>伊藤润二代表短片作品「至死不渝的爱」中，黑衣美少年也给人留下了非常深刻的印象。他浑身上下都散发着死亡的气息，其所在雾之町市遗留有一种叫“十字路口占卜”的风俗，人们会站在十字路口等最早通过那里的人，据他说的话来占卜“吉”或“凶”。而这位黑衣少年，往往会出现在弥漫着血雾的十字路口，面对那些无辜的问卜者，说出绝望的答案，直接导致无数自杀惨案的发生。</p>
        <p>一如白与黑、善与恶的对立，在「至死不渝的爱」余篇中登场的白衣美少年，虽然几乎有着和黑衣美少年一样的穿扮与外貌，但从他嘴里说出的占卜内容，往往能给人幸福感，让那些问卜者对生活充满了勇气和信心。可以说在同样决定命运的十字路口，白衣美少年则代表了善良的这一面。</p>
        <p><img alt="" src="https://android-imgs.25pp.com/fs08/2018/12/19/9/c6aa85ec5f3ebededd59bad378c8a3ce.png" class="content-single-image"></p>
        <p>一如美少年的黑白两面，「第五人格」中的监管者宿伞之魂，也拥有范无咎、谢必安两种形态，并且在服饰配色上也是黑与白的搭配。因此双面美少年的联动皮肤人选最终锁定为宿伞之魂，玩家在游戏中根据操控角色形态的变化，会呈现黑衣美少年和白衣美少年两种相貌。</p>
        <p>快到游戏里尽情享受这场庄园的盛宴吧！</p>
